import React from 'react';
import { styled } from '@mui/system';
import { Box, Typography } from '@mui/material';
import PrimaryBtnMD from '../../../../components/UI/Atoms/Button/PrimaryBtnMD';
import { useNavigate } from 'react-router-dom';
import { useModal } from '../../../../hooks/basic/useModal';
import ResistDojang from '../Dojang/ResistDojang/ResistDojang';
import { useRecoilValue } from 'recoil';
import userInfoAtom from '../../../../recoil/atom/login/userInfoAtom';
import { useDebug } from '../../../../hooks/basic/useDebug';

const BoxStyle = styled(Box)(({ theme }) => ({
  backgroundColor: '#FAFAFA',
  borderRadius: '10px',
  width: '405px',
  height: '522px',
  margin: '75px auto',
  display: 'flex',
  flexDirection: 'column',
  justifyContent: 'center',
  alignItems: 'center',
  gap: '50px',
}));

const NotExistDojang = () => {
  const navigate = useNavigate();
  const userInfo = useRecoilValue(userInfoAtom)
  // useDebug(userInfo)

  const [ openResist, openResistFn, closeResistFn ] = useModal()
  

  return (
    <>
    { userInfo?.user_role === 'SABUM' &&
      <BoxStyle>
        <Typography variant="h3">계정에 연결된 도장이 없습니다.</Typography>
        <div className="text-center">
          소속된 태권도장의 관장님에게 <br /> 
          초대장을 받아 보세요! <br /> 
          <br /> 
          받은 초대장은 태권월드 모바일 앱에서 <br />
          확인하실 수 있습니다. <br />
          <br />
          초대장 수락 후 다시 로그인 해주세요!
        </div>
      </BoxStyle>
    }

    { userInfo?.user_role === 'KWANJANG' &&
      <BoxStyle>
        <Typography variant="h3">등록된 도장이 없습니다.</Typography>
        <div className="text-center">
          도장 정보를 등록하고
          <br /> 태권월드 서비스를 이용해보세요!
        </div>
        <PrimaryBtnMD onClick={openResistFn}>
          도장 정보 등록
        </PrimaryBtnMD>
        <ResistDojang
          open={openResist}
          onClose={closeResistFn}
        />
      </BoxStyle>
    }
    </>
  );
};

export default NotExistDojang;
