import React from 'react';
import { useNavigate } from 'react-router-dom';
import { Typography } from '@mui/material';
import PageContainer from '../../../../components/layout/PageContainer';
import { useModal } from '../../../../hooks/basic/useModal';
import ExistDojang from './ExistDojang';
import NotExistDojang from './NotExistDojang';
import ModalContainerAlert from '../../../../components/layout/ModalContainerAlert';
import AlertText from '../../../../components/common/Alert/AlertText';
import { useQuery } from '@tanstack/react-query';
import { selectDojang } from '../../../../api/Dojang/dojangApi';
import Header from '../../../../components/layout/Header';
import { getInvitedInfo } from '../../../../api/Invite/inviteApi';
import PrimaryBtnMD from '../../../../components/UI/Atoms/Button/PrimaryBtnMD';
import WhiteBtnMD from '../../../../components/UI/Atoms/Button/WhiteBtnMD';

const SelectDojang = () => {
  const navigate = useNavigate();
  const [openLog, openFnLog, closeFnLog] = useModal();

  // const userInfo = useRecoilValue(userInfoAtom)
  const isLogin = sessionStorage.getItem('access_token');
  const { data } = useQuery(['selectDojang'], () => selectDojang(), {
    enabled: !!isLogin,
  });

  const { data: invitation } = useQuery(['getInvitedInfo'], () => getInvitedInfo());

  const handleInvite = () => {
    navigate('/select/invite')
  }
  // const userInfo = useRecoilValue(userInfoAtom)
  // useDebug(userInfo)
  // useDebug(data)

  return (
    <>
      <Header />
      <div className="mt-[30px]">
        {data && (
          <PageContainer width="md">
            { invitation?.invite_info?.length > 0 && (
              <div className="flex items-center gap-[120px] w-[830px] h-[50px] rounded-[10px] bg-[#fff] mt-[-30px] mb-[20px]">
                <Typography variant='h3' className='ml-[300px]'>새로운 초대장이 도착했습니다.</Typography>
                <WhiteBtnMD onClick={handleInvite} className='text-[16px] w-[140px] text-[#5e81f4]'>초대장 보러가기</WhiteBtnMD>
                {/* <PrimaryBtnMD onClick={handleInvite} className='text-[16px]'>초대장 보러가기</PrimaryBtnMD> */}
              </div>
            )}
            <div className="relative w-full flex items-center">
              <div className="w-full flex justify-center items-center">
                <Typography variant="h3">도장을 선택해주세요.</Typography>
              </div>
              <div
                className="flex gap-[6px] mt-[2px] mr-[36px] items-center cursor-pointer absolute right-0 top-0 border border-solid border-1px border-[#6e6e6e] border-x-0 border-t-0"
                onClick={openFnLog}
              >
                <img
                  src="https://tkdw-img.s3.ap-northeast-2.amazonaws.com/side_menu/logout.png"
                  alt="logout"
                  className="w-[20px] h-[20px]"
                />
                <Typography variant="h5">로그아웃</Typography>
              </div>
            </div>
            {data.length > 0 && <ExistDojang list={data} />}
            {data.length === 0 && <NotExistDojang />}

            <ModalContainerAlert
              open={openLog}
              onClose={closeFnLog}
              onConfirm={() => navigate('/logout')}
              confirmText="예"
            >
              <AlertText title>로그아웃 하시겠습니까?</AlertText>
              <AlertText>로그아웃 후 홈화면으로 돌아갑니다.</AlertText>
            </ModalContainerAlert>
          </PageContainer>
        )}
      </div>
    </>
  );
};

export default SelectDojang;
