import React, { useRef, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Col } from '../Student/component/StudentInfo/StudentForm/StudentForm';
import { styled } from '@mui/system';
import { Box } from '@mui/material';
import ModalContainerAlert from '../../../../components/layout/ModalContainerAlert';
import { useModal } from '../../../../hooks/basic/useModal';
import AlertText from '../../../../components/common/Alert/AlertText';
import { deleteDojang } from '../../../../api/Dojang/dojangApi';
import EditBtnSM from '../../../../components/UI/Atoms/Button/EditBtnSM';
import PrimaryBtnLG from '../../../../components/UI/Atoms/Button/PrimaryBtnLG';
import KeyboardDoubleArrowLeftIcon from '@mui/icons-material/KeyboardDoubleArrowLeft';
import KeyboardDoubleArrowRightIcon from '@mui/icons-material/KeyboardDoubleArrowRight';
import { phoneHyphenRegex } from '../../../../utils/lib/Regex';
import { useRecoilValue, useSetRecoilState } from 'recoil';
import dojangIdAtom from '../../../../recoil/atom/dojangIdAtom';
import dojangListAtom from '../../../../recoil/atom/dojangListAtom';
import LoadingModal from '../../../../components/UI/Atoms/Spinner/LoadingModal';
import dojangInfoAtom from '../../../../recoil/atom/dojangInfoAtom';
import { useDebug } from '../../../../hooks/basic/useDebug';
import userInfoAtom from '../../../../recoil/atom/login/userInfoAtom';
import { useMutation, useQuery } from '@tanstack/react-query';
import { resignSabum } from '../../../../api/Sabum/sabum';
import { getLinkedInfo } from '../../../../api/user';

const ListBox = styled(Box)(({ theme, slide }) => ({
  width: '405px',
  height: '520px',
  margin: 'auto',
  backgroundColor: '#ffffff',
  border: '1px solid #e7e7e7',
  padding: '30px 20px',
  boxShadow: '4px 4px 10px rgba(0, 0, 0, 0.05)',
  borderRadius: '10px',
  marginLeft: '5px',
  marginRight: '5px',
  transform: `translateX(${slide}px)`,
  transition: '0.5s all ease',
  '*': {
    boxSizing: 'border-box',
    cursor: 'pointer',
  },
}));

const labelStyle = {
  width: '88px',
  height: '40px',
  fontSize: '14px',
  color: '#00196A',
  padding: '10px 0 10px ',
};

const pStyle = {
  width: '267px',
  // height: '40px',
  padding: '0px 10px',
  fontSize: '14px',
  lineHeight: '20px',
  color: '#000000',
  border: 'none',
  borderBottom: '1px solid #e7e7e7',
  backgroundColor: '#ffffff',
};

const ImgStyle = styled(Box)(({ theme, props }) => ({
  width: '178px',
  height: '122px',
  backgroundImage: `url(https://tkdw-img.s3.ap-northeast-2.amazonaws.com/${props})`,
  backgroundSize: 'contain',
  backgroundRepeat: 'no-repeat',
  backgroundPosition: 'center',
  border: '1px solid #dddddd',
}));

const ExistDojang = ({ list }) => {
  const ref = useRef();
  const navigate = useNavigate();

  // state
  const [logout, setLogout] = useState(false);
  const [selectType, setSelectType] = useState('');
  const [slidePx, setSlidePx] = useState(0);
  const [loading, setLoading] = useState(false);

  // recoil
  const userInfo = useRecoilValue(userInfoAtom)
  const setDojangId = useSetRecoilState(dojangIdAtom)
  const setDojangList = useSetRecoilState(dojangListAtom)  
  const setDojangInfo = useSetRecoilState(dojangInfoAtom)

  // custom hook
  const [openDel, openDelFn, closeDelFn] = useModal();
  const [openResign, openResignFn, closeResignFn] = useModal();

  // query
  const { mutate: delDojangMT, isLoading} = useMutation((dojangId) => deleteDojang(dojangId));
  const { mutate: resignSabumMT, onLoading} = useMutation(({dojangId, sabumId}) => resignSabum(dojangId, sabumId));
  const { data } = useQuery(['getLinkedInfo'], () => getLinkedInfo())

  // handler
  const handleClick = (e, name) => {
    const clickIdx = Number(e.currentTarget.id);
    let temp = list[clickIdx];
    setSelectType(temp);
  };

  const handleDelete = () => {
    delDojangMT(selectType.id, {
      onSuccess: (res) => {
        alert('도장이 삭제 되었습니다.')
        window.location.reload(true);
      }
    });
  };

  const handleResign = (e) => {
    ref.current = e.target.id;
    openResignFn();
  };

  const handleConfirmResign = () => {
    console.log(ref.current, data.id)
    let temp = {
      dojangId : ref.current,
      sabumId: data.id
    }
    resignSabumMT(temp, {
      onSuccess: (res) => {
        alert('퇴소 되었습니다.')
        window.location.reload(true);
      }
    });
  }

  const handleLogout = () => {
    navigate('/logout');
  };


  const toPrev = () => {
    slidePx < 0 && setSlidePx(slidePx + 415);
  };

  const toNext = () => {
    slidePx >= -((list.length / 2 - 1) * 830) && setSlidePx(slidePx - 415);
  };

  const handleSelect = (e) => {
    if (selectType) {
      setLoading(true);

      setDojangId(selectType.id)
      setDojangList(list)
      setDojangInfo(selectType)

      setTimeout(() => navigate('/home'), 1000)
    } else {
      alert('도장을 선택해 주세요.');
    }
  };

  // useDebug(list)

  return (
    <>
      { list?.length <= 2 &&
        <div className="flex w-[830px] h-[630px] relative">
          <div
            className="flex justify-center w-[830px] overflow-hidden "
            // style={{ }}
          >
            {React.Children.toArray(
              list.map((item, idx) => (
                <ListBox
                  // style={style}
                  id={idx}
                  className={selectType?.id == item.id ? 'type_selected' : ''}
                  onClick={handleClick}
                  slide={slidePx}
                >
                  <div className="mb-[20px]">
                    <Col>
                      <label style={labelStyle}></label>
                      <ImgStyle props={item?.logo_img ? JSON.parse(item?.logo_img)[0] : ''} />
                    </Col>
                  </div>
                  <div>
                    <div>
                      <Col>
                        <label style={labelStyle}>도장 이름</label>
                        <p style={pStyle} name="name">
                          {item.name}
                        </p>
                      </Col>
                    </div>
                    <div>
                      <Col>
                        <label style={labelStyle}>도장 전화번호</label>
                        <p style={pStyle} name="phone_number">
                          {phoneHyphenRegex(item.phone_number)}
                        </p>
                      </Col>
                    </div>
                    <div>
                      <Col>
                        <label style={labelStyle}>도장 주소</label>
                        <p style={pStyle} name="address_name">
                          {item.address_name}
                        </p>
                      </Col>
                    </div>
                    <div>
                      <Col>
                        <label style={labelStyle}></label>
                        <p style={pStyle} name="address_detail">
                          {item.address_detail}
                        </p>
                      </Col>
                    </div>
                    <div>
                      <Col>
                        <label style={labelStyle}>소속 사범님 수</label>
                        <p style={pStyle} name="sabum_count">
                          {item.sabum_count}
                        </p>
                      </Col>
                    </div>
                    <div>
                      <Col>
                        <label style={labelStyle}>소속 수련생 수</label>
                        <p style={pStyle} name="student_count">
                          {item.student_count}
                        </p>
                      </Col>
                    </div>
                  </div>
                  { userInfo?.user_role === 'KWANJANG' &&
                    <div className="text-right">
                      <EditBtnSM onClick={openDelFn}>삭제</EditBtnSM>
                    </div>
                  }
                  { userInfo?.user_role === 'SABUM' &&
                    <div className="text-right">
                      <EditBtnSM id={item.id} onClick={handleResign} className='border-[#ff0000] text-[#ff0000]'>퇴소</EditBtnSM>
                    </div>
                  }
                </ListBox>
              )),
            )}
          </div>
        </div>
      }

      { list?.length > 2 &&
        <div className="flex w-[830px] h-[630px] relative">
          <KeyboardDoubleArrowLeftIcon
            onClick={toPrev}
            sx={{
              display: slidePx === 0 ? 'none' : '',
              position: 'absolute',
              left: '-50px',
              top: 'calc(50% - 20px)',
              color: '#2F5DF2',
              fontSize: '40px',
              cursor: 'pointer',
            }}
          />
          <div
            className="flex w-[830px] overflow-hidden "
            // style={{ }}
          >
            {React.Children.toArray(
              list.map((item, idx) => (
                <ListBox
                  // style={style}
                  id={idx}
                  className={selectType?.id == item.id ? 'type_selected' : ''}
                  onClick={handleClick}
                  slide={slidePx}
                >
                  <div className="mb-[20px]">
                    <Col>
                      <label style={labelStyle}></label>
                      <ImgStyle props={item?.logo_img ? JSON.parse(item?.logo_img)[0] : ''} />
                    </Col>
                  </div>
                  <div>
                    <div>
                      <Col>
                        <label style={labelStyle}>도장 이름</label>
                        <p style={pStyle} name="name">
                          {item.name}
                        </p>
                      </Col>
                    </div>
                    <div>
                      <Col>
                        <label style={labelStyle}>도장 전화번호</label>
                        <p style={pStyle} name="phone_number">
                          {phoneHyphenRegex(item.phone_number)}
                        </p>
                      </Col>
                    </div>
                    <div>
                      <Col>
                        <label style={labelStyle}>도장 주소</label>
                        <p style={pStyle} name="address_name">
                          {item.address_name}
                        </p>
                      </Col>
                    </div>
                    <div>
                      <Col>
                        <label style={labelStyle}></label>
                        <p style={pStyle} name="address_detail">
                          {item.address_detail}
                        </p>
                      </Col>
                    </div>
                    <div>
                      <Col>
                        <label style={labelStyle}>소속 사범님 수</label>
                        <p style={pStyle} name="sabum_count">
                          {item.sabum_count}
                        </p>
                      </Col>
                    </div>
                    <div>
                      <Col>
                        <label style={labelStyle}>소속 수련생 수</label>
                        <p style={pStyle} name="student_count">
                          {item.student_count}
                        </p>
                      </Col>
                    </div>
                  </div>
                  { userInfo?.user_role === 'KWANJANG' &&
                    <div className="text-right">
                      <EditBtnSM onClick={openDelFn}>삭제</EditBtnSM>
                    </div>
                  }
                  { userInfo?.user_role === 'SABUM' &&
                    <div className="text-right">
                      <EditBtnSM id={item.id} onClick={handleResign} className='border-[#ff0000] text-[#ff0000]'>퇴소</EditBtnSM>
                    </div>
                  }
                </ListBox>
              )),
            )}
          </div>
          <KeyboardDoubleArrowRightIcon
            onClick={toNext}
            sx={{
              display: slidePx <= -((list.length / 2 ) * 415) - 415 ? 'none' : '',
              position: 'absolute',
              right: '-50px',
              top: 'calc(50% - 20px)',
              color: '#2F5DF2',
              fontSize: '40px',
              cursor: 'pointer',
            }}
          />
        </div>
      }
      <div className="my-[-10px]">
        {/* <SecondBtnLG className="text-[17px] w-[111px] h-[50px]">뒤로가기</SecondBtnLG> */}
        <PrimaryBtnLG className="text-[17px] w-[150px] h-[40px] ml-[10px]" onClick={handleSelect}>
          선택
        </PrimaryBtnLG>
      </div>

      <ModalContainerAlert
        open={openDel}
        onClose={closeDelFn}
        onConfirm={handleDelete}
        confirmText='삭제'
        isLoading={isLoading}
        height={400}
      >
        <AlertText title>
          해당 도장을 삭제 합니다.
        </AlertText>
        <AlertText>
          삭제하면 복구가 불가능 합니다. <br />
          도장에 소속 사범, 소속 수련생이 존재할 경우 삭제할 수 없습니다.<br />
          소속 사범, 소속 수련생을 퇴사/퇴소 하고 삭제해주세요. <br />
          삭제 하시겠습니까?
        </AlertText>
      </ModalContainerAlert>
      
      
      <ModalContainerAlert
        open={openResign}
        onClose={closeResignFn}
        onConfirm={handleConfirmResign}
        confirmText='퇴소'
        isLoading={onLoading}
        height={400}
      >
        <AlertText title>
          해당 도장을 퇴소 합니다.
        </AlertText>
        <AlertText>
          퇴소 하시겠습니까?
        </AlertText>
      </ModalContainerAlert>

      {loading && <LoadingModal />}
    </>
  );
};
export default ExistDojang;
